<script>
import $store from "../store.js"
import $bus from "../../bus.js"
import { allowedPNG } from "../WeekModel";

export default {
    data() {
        return {
            store: $store,
            allowedPNG
        }
    },

    computed: {
        c_tags() {
            return this.store.tags.stats.sortBy(ti => ti.tag);
        }
    },

    methods: {
        searchWord() {
            $bus.$emit("search-word");
        },

        setTagged(ti) {
            $bus.$emit("set-tagged", ti);
        }
    }
};
</script>

<template>
    <div class="tag-bar">
        <div class="tag-bar-item" v-for="ti in c_tags" :key="ti.tag">
            <div :class="'wi-outer wi-inner ' + ti.tag.replace('#','')"
                v-if="allowedPNG.includes(ti.tag.replace('#', ''))"
            ></div>
            <a href v-if ="ti && ti.tag && ti.weekNums.length" @click.prevent="setTagged(ti)" class="tag-link" :class="{'strong': ti.weekNums.length > 20, 'tag-selected': ti.tag == store.curTag}">
                {{ti.tag}} ({{ti.weekNums.length}})
            </a>
        </div>
        <md-field>
            <label></label>
            <md-input type="text" v-model="store.searchedWord" style="width:300px" />
        </md-field>
        <div style="text-align:center">
            <md-button type="button" @click="searchWord()">  </md-button>
        </div>
    </div>
</template>

<style lang="less">
@import "../assets/wol-vars.less";

.tag-bar {
    float: right;
    position: absolute;
    left: 1140px;
    top: 0;
    background-color: #f0f0f0;
    border-radius: 4px;
    border: 3px solid transparent;
    list-style: none;
    padding: 10px 15px;
    width: 300px;

    .tag-bar-item {
        display:inline-block;
        margin-right:12px;

        .wi-outer {
            position:relative;
            top: 2px;
        }
    }

    a:hover {
        text-decoration: none;
        color: blue;
    }

    a,
    a:visited,
    a:active {
        text-decoration: none;
        font-size: 0.9em;
        color: #448aff;
    }

    a.tag-link.tag-selected {
        font-weight: bold;
        color: @color-selected !important;
    }
}
</style>


