<script>
import $store from "../store.js"

export default {
    data() {
        return {
            store: $store
        }
    },

    computed: {
        c_infoed() {
            return this.store.weeks.reduce((memo, w) => +(!!w.info & !w.future) + memo, 0);
        }
    }
}
</script>

<template>    
    <div class="wol-header">
        <div class="lived">
            <strong> {{store.lived.percentage}}</strong>, : <strong>{{store.lived.weeks}}</strong>, : <strong>{{store.lived.days}}</strong>, : <strong>{{store.lived.hours}}</strong>
        </div>
        <div class="weeks-infoed">
             : <strong>{{c_infoed}} ({{((c_infoed / store.lived.weeks) * 100).toFixed(1) + '%'}})</strong>
        </div>
        <div class="remained" style="">
            <strong> {{store.remained.percentage}}</strong>, : <strong>{{store.remained.weeks}}</strong>, : <strong>{{store.remained.days}}</strong>, : <strong>{{store.remained.hours}}</strong>
        </div>
    </div>    
</template>

<style lang="less">
.wol-header {
    padding-bottom: 30px;
    padding-top:58px;

    .lived {
        float:left;
    }

    .weeks-infoed {
        color: #448aff;
        margin-left: 45px;
        float:left;
    }

    .remained {
        float: right;
        position:relative;
        left:-27px
    }
}
</style>




