<script>
import { LOG, ERROR, FIX_TIME } from '../../logs.js'
import { authenticate } from '../../fb.js'
import $bus from '../../bus.js'
import $store from '../store.js'
import $config from '../../config.js'
import WeekModel from '../WeekModel.js'
import WolSpinner from '../../WolSpinner.vue'
import WolHeader from './WolHeader.vue'
import WolTagbar from './WolTagbar.vue'
import WeekItem from './WeekItem.vue'
import EditDialog from './EditDialog.vue'
import MapDialog from './MapDialog.vue'
import WolNavbar from '../../WolNavbar.vue'

export default {
    data() {
        return {
            store: $store,
            config: $config
        }
    },

    components: {WeekItem, EditDialog, MapDialog, WolSpinner, WolHeader, WolTagbar, WolNavbar},

    computed: {
        c_tags() {
            return this.store.tags.stats.sortBy(ti => ti.tag);
        },
    },

    created() {
        this.store.loading = true;
        this.init();

        this.$watch("store.curWeek", this.watcherCurWeek);
        this.$watch("store.shownEditDialog", this.watcherShownEditDialog);
    },

    mounted() {
        LOG("mounted()", 'WolApp MOUNTED');
        $bus.$on("week-created", this.createWeek);
        $bus.$on("week-clicked", this.editWeek);
        $bus.$on("wit", this.checkWiTime);
        $bus.$on("show-map-dialog", this.showMapDialog);
        $bus.$on("show-pills-dialog", this.showPillsDialog);
        $bus.$on("show-messages", this.showMessages);
        $bus.$on("week-saved", this.saveWeek);
        $bus.$on("prev-week", this.prevWeek);
        $bus.$on("next-week", this.nextWeek);
        $bus.$on("set-tagged", this.setTagged);
        $bus.$on("search-word", this.searchWord);
    },

    updated() {
        if (this.store.loading) {
            this.store.loading = false;
            const avg = this.store.test.wiTimes.reduce((memo, num) => memo + num, 0) / this.store.test.wiTimes.length;
            LOG('updated()', `WolApp RENDERED. Avg week-item render time: ${avg/1000} ms`);
        }
    },

    methods: {
        watcherCurWeek(newValue){
            let query = this.$router.history.current.query || {};
            if (!newValue) {
                delete query.w;
            }else  {
                query.w = newValue.weekNum;
            }
            this.$router.replace({query}).catch(err => { if (err.name != "NavigationDuplicated") { console.dir(err); }});
        },

        watcherShownEditDialog(newValue){
            let query = this.$router.history.current.query || {};
            query.edit = newValue;
            this.$router.replace({query}).catch(err => { if (err.name != "NavigationDuplicated") { console.dir(err); }});
        },

        async init() {
            LOG('init()', "DATA REQUESTED");
            try {
                const response = await axios.get(`${this.config.baseUrl}/api/wol/weeks`);
                if (response.data) {
                    LOG('init()', "DATA RECEIVED");
                    this.initData(response.data);
                    LOG('init()', "DATA_INITIALIZED, START RENDERING");
                } else {
                    ERROR('init()', "loading /api/wol/weeks failed")
                }
            } catch (err) {
                ERROR('created()', "loading /api/wol/weeks failed", err);
            }
            this.readQueryParams();
        },

        readQueryParams() {
            let query = this.$router.history.current.query;
            if (query.w && query.edit) {
                this.editWeek(this.weeks[+query.w]);
            }
        },
        
        checkWiTime(time) {
            this.store.test.wiTimes.push(time);
        },

        error(msg, response, fname) {
            if (fname) {
                ERROR(fname, msg, response);
            } else {
                FIX_TIME(`[ERROR]: ${msg}`);
            }
            toastr.error(msg);
        },

        success(msg, fname) {
            if (fname) {
                LOG(fname, `[SUCCESS]: ${msg}`);
            } else {
                FIX_TIME(`[SUCCESS]: ${msg}`);
            }
            toastr.success(msg);
        },

        checkGoogleMapsLoaded() {
            this.store.googleMapsLoaded = true;
        },

        initData(data) {
            //   
            data.spans.forEach(span => {
                span.startTime = (new Date(span.start)).getTime();
                span.endTime = (new Date(span.end)).getTime();
            });

            const nowTime = (new Date()).getTime();
            const birthTime = (new Date(data.birthdate)).getTime(); //,   (  00:00) <int>
            const deathTime = (new Date(data.deathdate)).getTime(); // <int>
            const whereIsNow = Math.min(deathTime, nowTime); //"  " =     <int>

            //  /
            const daysLived = Math.round((whereIsNow - birthTime) / (1000 * 60 * 60 * 24));
            const daysRemained = Math.max(Math.round((deathTime - nowTime) / (1000 * 60 * 60 * 24)), 0);

            this.store.lived = {
                percentage: `${((daysLived / (daysLived + daysRemained)) * 100).toFixed(2)}%`,
                days: daysLived,
                weeks: Math.ceil(daysLived / 7),
                hours: Math.round((whereIsNow - birthTime) / (1000 * 60 * 60))
            };

            this.store.remained = {
                percentage: `${((daysRemained / (daysLived + daysRemained)) * 100).toFixed(2)}%`,
                days: daysRemained,
                weeks: Math.floor(daysRemained / 7),
                hours: daysRemained * 24
            };

            let weekNum = 0; //    
            //  ,   <int>
            let startMoment = moment(data.birthdate).startOf("week"); // 
            let weeks = [];
            let curWeek = null;
            while (startMoment._d.getTime() < deathTime) {
                weekNum++; // 
                let endMoment = startMoment.clone().add(1, 'week').subtract(1, 'second'); // 
                let dbWeek = data.weekInfo[weekNum];
                let week = new WeekModel(startMoment, endMoment, weekNum, (dbWeek ? dbWeek.info : ""), data.spans);
                //  
                if (weekNum === 1 && week.yearNum > 1) {
                    this.store.firstYearPlaceHolderWidth = (week.yearNum - 1) * 20 - 3;
                }
                //   
                if (startMoment._d.getTime() > nowTime && !curWeek) {
                    curWeek = weeks[weeks.length - 1];
                }
                weeks.push(week);
                endMoment.add(1, 'second');
                startMoment = endMoment;
            }

            this.tagsUpdateAll(weeks);
            this.store.years = _.groupBy(weeks, 'year'); //   
            this.store.weeks = weeks; //              
            this.store.curWeek = curWeek; // 
        },

        //------------------------------ -----------------------------------------------------------
        //   
        showMessages() {
            this.store.shownMessageDialog = true;
            this.store.curMessages = this.store.curWeek.messages;
        },

        //  
        showEditDialog() {
            if (!this.store.shownEditDialog) {
                this.store.shownEditDialog = true;
            }
        },

        //   
        showMapDialog() {
            if (!this.store.shownMapDialog) {
                this.store.shownMapDialog = true;
            }
        },

        showPillsDialog() {
            if (!this.store.shownPillsDialog) {
                this.store.shownPillsDialog = true;
            }
        },        

        //----------------------------     -----------------------------------------------------
        //     
        countUninfoed(yearWeeks) {
            return yearWeeks.reduce((memo, week) => memo + +(!week.info & !week.future), 0);
        },

        //----------------------------      -----------------------------------------
        /**
         *     
         * @param week
         */
        createWeek(week) {
            this.store.weeks[week.weekNum] = week;
        },

        /**
         *  
         */
        editWeek(week) {
            const oldWeekNum = this.store.curWeek.weekNum;
            if (oldWeekNum !== week.weekNum && oldWeekNum > 0) {
                this.store.weeks[oldWeekNum].selected = false;
            }
            week.selected = true;
            LOG('editWeek', `Current week is ${week.weekNum}`);
            this.store.curWeek = week;
            this.store.curWeek.editInfo = week.info;
            $bus.$emit("curweek-changed");
            this.showEditDialog();
            this.loadMessages(week);
            this.loadPhotos(week);
            //this.writeQueryParams();
        },

        async loadPhotos(week, forced) {
            if (week.photos.length > 0 && !forced)
                return;
            week.msgLoading = true;
            try {
                const response = await axios.get(`${this.config.baseUrl}/api/wol/photos/${week.weekNum}`);
                if (response.data) {
                    week.photos = response.data;
                    week.photoLoading = false;
                    week.photoCount = week.photos.length;
                    LOG('loadPhotos', `Messages loaded for week ${week.weekNum}, count ${week.photoCount}`);
                }
            } catch (err) {
                //this.error(`cannot load messages for week ${week.weekNum}`, err, 'loadPhotos');
                ERROR("loadPhotos", `cannot load messages for week ${week.weekNum}`, err);
            };    
        }, 

        /**
         *    
         * @param week
         * @param forced
         */
        async loadMessages(week, forced) {
            if (week.messages.length && !forced)
                return;
            week.msgLoading = true;
            try {
                const response = await axios.get(`${this.config.baseUrl}/api/msg/${week.weekNum}`);
                if (response.data) {
                    week.messages = response.data;
                    week.msgLoading = false;
                    week.msgCount = week.messages.reduce((memo, chat) => chat.messages.length + memo, 0);
                    LOG('loadMessages', `Messages loaded for week ${week.weekNum}, count ${week.msgCount}`);
                }
            } catch (err) {
                this.error(`cannot load messages for week ${week.weekNum}`, err, 'loadMessages');
            };
        },

        /**
         *   
         */
        async saveWeek() {
            const week = this.store.curWeek;
            if (!week) return;
            this.store.weeks[week.weekNum].info = week.editInfo;
            try {
                const response = await axios.post(`${this.config.baseUrl}/api/wol/weeks`, {
                    name: this.config.name,
                    weekNum: week.weekNum,
                    info: week.info,
                    msgCount: week.msgCount
                });
                if (response.status === 200) {
                    this.success(`Week ${week.weekNum} saved to DB.`, 'saveWeek');
                    this.tagsUpdate(week);
                    //this.writeQueryParams();
                } else {
                    this.error('Error saving week:', response, 'saveWeek');
                }
            } catch (err) {
                this.error(`Error saving week:`, err, 'saveWeek');
            };
        },

        //--------------------------     ------------------------------------------------------
        /**
         *  
         */
        prevWeek() {
            if (!this.store.curWeek && this.store.curWeek.weekNum === 1) {
                return;
            }
            const week = this.store.weeks.find(week => week.weekNum == this.store.curWeek.weekNum - 1);
            if (week) {
                this.editWeek(week);
            }
        },

        /**
         *  
         */
        nextWeek() {
            if (!this.store.curWeek) {
                return;
            }
            const week = this.store.weeks.find(week => week.weekNum == this.store.curWeek.weekNum + 1);
            if (week && !week.future) {
                this.editWeek(week);
            }
        },

        /**
         *   
         */
        forwardYear() {
             if (!this.store.curWeek) {
                return;
            }
            const week = this.store.weeks.find(week => week.yearNum == this.store.curWeek.yearNum && week.year == this.store.curWeek.year + 1);
            if (week && !week.future) {
                this.editWeek(week);
            }
        },

        /**
         *   
         */
        backwardYear() {
            if (!this.store.curWeek) {
                return;
            }
            const week = this.store.weeks.find(week => week.yearNum == this.store.curWeek.yearNum && week.year == this.store.curWeek.year - 1);
            if (week) {
                this.editWeek(week);
            }
        },

        //---------------------  ---------------------------------------------------------------------------------
        tagsUpdateAll(weeks) {
            const stats = [];
            weeks.forEach(week => {
                week.getTags().forEach(tag => {
                    const tagInfo = stats.find(ti => ti.tag == tag);
                    if (!tagInfo) {
                        stats.push({
                            tag: tag,
                            weekNums: new Set([week.weekNum])
                        });
                    } else {
                        tagInfo.weekNums.add(week.weekNum);
                    }
                })
            });
            this.store.tags.stats = stats;
        },

        /**
         *  ,   
         * @param week
         */
        tagsUpdate(week) {
            const stats = this.store.tags.stats;
            const num = week.weekNum;
            //    
            let ti = week.getTags().find(tag => stats.find(ti => ti.tag == tag));
            if (!ti) {
                stats.push({ tag, weekNums: new Set([num]) });
            }
            if (this.isCurrentTag(ti)) {
                week.tagged = true;
            }
            //    
            stats.forEach(ti => {
                if (!week.hasTag(ti.tag) && ti.weekNums && ti.weekNums.has(num)) {
                    ti.weekNums.delete(num);
                    if (week.tagged && this.isCurrentTag(ti)) {
                        week.tagged = false;
                    }
                }
                //     
                else {
                    if (week.hasTag(ti.tag)) {
                        ti.weekNums.add(num);
                    }
                    if (!week.tagged && this.isCurrentTag(ti)) {
                        week.tagged = true;
                    }
                }
            });
        },


        /**
        *    
         */
        searchWord() {
            this.clearTagged();
            const word = this.store.searchedWord.toLowerCase();
            this.store.weeks.forEach(week => {
                if (week.info.toLowerCase().includes(word)) {
                    week.tagged = true;
                }
            });
        },

        /**
         *    
         * @param ti
         * @returns {*|boolean}
         */
        isCurrentTag(ti) {
            return this.store.tags.current && this.store.tags.current.tag === ti.tag;
        },

        /**
         *   / 
         * @param ti - tagInfo
         */
        setTagged(ti) {
            const turnoff = this.isCurrentTag(ti);
            this.clearTagged();
            if (!turnoff) {
                this.store.tags.current = ti;
                this.store.curTag = ti.tag;
                ti.weekNums.forEach(index => {
                    this.store.weeks[index].tagged = true;
                });
            }
        },


        /**
         *   
         */
        clearTagged() {
            this.store.tags.current = undefined;
            this.store.curTag = "";
            this.store.weeks.forEach(week => {
                if (week.tagged) {
                    week.tagged = false;
                }
            });
        },
    }
}
</script>

<template>
<div class="app-layout">
    <wol-spinner :store="store"></wol-spinner> 
    <transition name="fade">
        <div class="wol-container" v-show="!store.loading">
            <wol-header></wol-header>
            <div style="position:relative">
                <table class="year-table clearfix">
                    <tr v-for="(year_weeks, year, index) in store.years" :key="year">
                        <td class="year-row year-header" >{{year}}<span class="uninfoed" v-if="countUninfoed(year_weeks)">({{countUninfoed(year_weeks)}})</span></td>
                        <td class="year-row year-weeks">
                            <div class="week-placeholder" v-if="index === 0" :style="'width:' + store.firstYearPlaceHolderWidth + 'px'"></div>
                            <week-item v-for="week in year_weeks" :key="week.weekNum" :week="week"></week-item>
                        </td>
                    </tr>
                </table> 
                <wol-tagbar></wol-tagbar>        
            </div>            
        </div>
    </transition>
    <edit-dialog></edit-dialog>
    <map-dialog></map-dialog>
</div>
</template>

<style lang="less">
@import "../assets/wol-vars.less";

.app-layout {
    background-color: @window-background;
}

.wol-container {
    width: 1120px;
    margin: 0 auto;
}

.year-table {
    margin: 0;
    width: 1120px;
}

.year-row {
    height: 20px;
    vertical-align: middle;

    &.year-header {
        font-family: Roboto;
        color: @window-foreground;
        font-weight: bold;
        display: inline-block;
        width: 50px;
        font-size: 15px;
    }
}

.uninfoed {
    color: @color-gray;
    font-size: 10px;
    font-weight: normal;
    position: relative;
    top: -3px;
}

.week-placeholder {
    border: 0;
    margin: 0;
    display: inline-block;
    background: @window-background;
}

/* BASIC CLASSES */
body {
    font-family: Helvetica, Arial, sans-serif;
    font-size: 14px !important;
    line-height: 20px !important;
    color: #333;
    background-color: @window-background;
    box-sizing: initial;
    -webkit-font-smoothing: none !important;
}

li {
    line-height: 24px;
}

ul {
    padding-left: 0;
}

.strong {
    font-weight: bold;
}

/* MAIN WINDOW */
.loading {
    width: 800px;
    height: 600px;
    margin: 400px auto;
    text-align: center;
    z-index: 1000;
}

.caption {
    font-size: 64px;
    font-weight: bold;
    text-align: center;
}

/* dialog.css */
.md-dialog {
    z-index: 100;
    border-radius: 4px;
    transform: translate(-50%, -50%) !important;
}

.md-dialog-content {
    padding-bottom: 0;
}

.md-dialog-actions {
    min-height: 48px;
    padding: 0 24px 8px 8px;
}

.md-button-content {
    margin-left: -10px;
    margin-right: 6px;
}

.md-tabs-navigation .md-ripple {
    padding: 0;
}

.md-overlay {
    z-index: 5 !important;
}

.md-button,
.md-button-clear {
    font-size: 13px !important;
}

.md-field {
    min-height: 36px;
    margin-bottom: 12px;
}

.md-tabs-navigation .md-button-content {
    position: static;
    margin-left: 2px;
}

.md-tabs-navigation>.md-button {
    text-transform: none;
    max-width: 160px !important;
    min-width: 130px !important;
    font-size: 12px !important;
    border: 1px solid #eee;
}

.md-title {
    line-height: normal;
}

td.md-table-cell {
    height: 20px;
}

.fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active   2.1.8 */ {
    opacity: 0;
}

.ib {
    display: inline-block;
}

.pl {
    float: left;
}

.pr {
    float: right;
}

.pull-left {
    float: left;
}

.pull-right {
    float: right;
}
</style>






