<script>
import $store from "../store.js"
import { LOG } from "../../logs.js"
import $config from "../../config.js"

export default {
	data() {
        return {
            store: $store,
			baseUrl: $config.baseUrl,
			isInited: false,
			isEdited: false,
			currentMarker: null,
			editedTitle: "",
			shownTitle: ""
        }
    },

	updated() {
		if (this.store.shownMapDialog && !this.store.isInited) {
			this.initTravels();
		}
	},

	methods: {
		updateMarker() {
			if (this.currentMarker) {
				this.currentMarker.title = this.editedTitle;
				this.isEdited = false;
				//this.editTravel(this.currentMarker);
			}
		},
		
		async saveTravel(pos, callback) {
            try {
                const response = await axios.post(`${this.baseUrl}/api/travels`, pos);
                if (response.status === 200) {
                    LOG('saveTravel', "coords lat=" + pos.lat + "; lng=" + pos.lng + " successfully saved");
                    pos._id = response.data._id;
                    callback(pos);
                } else {
                    console.log(response.status);
                }
            } catch (err) {
                this.error("Error saving coords", err, "saveTravel")
            }
        },

        async removeTravel(_id) {
            try {
                const response = await axios.delete(`${this.baseUrl}/api/travels/${_id}`);
                if (response.status === 200) {
                    LOG("removeTravel", `travel _id = ${_id} successfully removed`);
                } else {
                    console.log(response.status);
                }
            } catch (err) {
                this.error("Error deleting travel: ", err, 'removeTravel');
            }
        },

		async editTravel(marker) {
			try {
				const response = await axios.put(`${this.baseUrl}/api/travels/${marker._id}`, marker);
				if (response.status === 200) {
                    LOG("editTravel", `travel _id = ${marker._id} successfully updated`);
                } else {
                    console.log(response.status);
                }
			}catch (err) {
                this.error("Error updating travel: ", err, 'editTravel');
            }
		},

        async initTravels() {
            LOG("initMap", "start");
            const self = this;
            try {
                const response = await axios.get(`${this.baseUrl}/api/travels`);
                const travels = response.data.filter(t => t._id);
                const map = new google.maps.Map(document.getElementById('map'), {
                    zoom: 4,
                    center: travels[travels.length - 1]
                });

				let toXY = function (latLng) {
                    var numTiles = 1 << map.getZoom();
                    var projection = map.getProjection();
                    var worldCoordinate = projection.fromLatLngToPoint(latLng);
                    var pixelCoordinate = new google.maps.Point(
                            worldCoordinate.x * numTiles,
                            worldCoordinate.y * numTiles);

                    var topLeft = new google.maps.LatLng(
                        map.getBounds().getNorthEast().lat(),
                        map.getBounds().getSouthWest().lng()
                    );

                    var topLeftWorldCoordinate = projection.fromLatLngToPoint(topLeft);
                    var topLeftPixelCoordinate = new google.maps.Point(
                            topLeftWorldCoordinate.x * numTiles,
                            topLeftWorldCoordinate.y * numTiles);

                    return [ pixelCoordinate.x - topLeftPixelCoordinate.x, pixelCoordinate.y - topLeftPixelCoordinate.y]
                }


                let createMarker = (travel) => {
                    const marker = new google.maps.Marker({
                        position: travel,
                        title: "",
                        map
                    });

                    marker._id = travel._id;

                    google.maps.event.addListener(marker, "dblclick", function () {
                        self.removeTravel(marker._id);
                        marker.setMap(null);
                    });

					google.maps.event.addListener(marker, "rightclick", function(event) {
						self.isEdited = true;
						 self.isEdited = true;
                         var el = document.getElementById("editMarkerForm");
                         el.style.left = posX + 50;
                         el.style.top = posY + 50;
						 self.currentMarker = marker;						
					});
                }

                travels.forEach(createMarker);
                google.maps.event.addListener(map, 'click', (event) => {
                    self.saveTravel({
                        lat: event.latLng.lat(),
                        lng: event.latLng.lng()
                    }, 
                    createMarker);
                });
                LOG("initMap", `map loaded: ${travels.length} travels`);
				this.store.isInited = true;

            } catch (err) {
                LOG('initMap', `initMap failed: ${err}`);
            }
        }
	}
}
</script>
<template>
	<md-dialog class="map-dialog" :md-active.sync="store.shownMapDialog">
		<md-dialog-content>
			<div id="map" ></div>
            <div id="editMarkerForm" v-show="isEdited">
				<md-field>
            		<md-input v-model="editedTitle" @keyup.enter="updateMarker()"/>
				</md-field>
		    </div> 
		</div>
		</md-dialog-content>        
		<md-dialog-actions>
			<md-button @click="store.shownMapDialog = false"></md-button>
		</md-dialog-actions>
	</md-dialog>
</template>

<style lang="less">
/* MAP DIALOG */
#editMarkerForm {
	position:relative;
	width:150px;
	padding:5px;
}

.map-dialog {
    z-index: 100;
    border-radius: 4px;
    transform: translate(-50%, -50%) !important;
}
#map {
    width: 800px;
    height: 600px;
    background-color: grey;
}
</style>



