<script>
import $store from "../store.js";
import $bus from "../../bus.js";
import UAParser from "UAParser";

export default {
    data() {
        return {
            store: $store,
            isMobile: false
        };
    },

    computed: {
        c_tagIcons() {
            return (this.store.curWeek) ? this.store.curWeek.getTagIcons(): []
        }, 
        c_photos() {
            return (this.store.curWeek) ? this.store.curWeek.photos.sortBy(x => x.date): [];
        }
    },

    mounted() {
        $bus.$on("curweek-changed", this.clearCurChat);
        $bus.$on("curweek-changed", this.adjustArea);
        const device = new UAParser().getDevice();
        this.isMobile = device && (device.type == "mobile" || device.type == "tablet");
    },

    methods: {
         showChat(chat) {
            this.store.curWeek.curChat = chat;
            document.getElementById("chat_column").scrollTop = 0;
        },

        clearCurChat() {
            this.store.curWeek.curChat = { messages: [], chat: "" };
        },

        onKeydown(event) {
            if (!this.isMobile &&
                event.key == "Enter" &&
                !event.altKey &&
                !event.shiftKey &&
                !event.ctrlKey
            ) {
                event.preventDefault();
                this.onSave(true);
            } else {
                if (event.keyCode == 39 && event.ctrlKey) {
                    //right arrow
                    this.goNext();
                }
                if (event.keyCode == 37 && event.ctrlKey) {
                    //left arrow
                    this.goPrevious();
                }
            }
        },

        adjustArea() {
            this.$nextTick(() => {
                let el = document.getElementById("week_info_textarea");
                if (el) {
                    el.style.height = "1px";
                    el.style.height = `${Math.round(el.scrollHeight) + 6}px`;
                }
            });
        },

        goPrevious() {
            if (this.store.curWeek.weekNum > 1)
            { 
                $bus.$emit("prev-week");
            }
        },

        goNext() {
            $bus.$emit("next-week");
        },

        onSave(exit) {
            $bus.$emit("week-saved");
            if (exit) {
                this.store.shownEditDialog = false;
            }
        },

        onShowMessages() {
            this.store.curWeek.photoShown = false;
            if (this.store.curWeek.messages.length) {
                this.store.curWeek.msgShown = !this.store.curWeek.msgShown;
            }            
        },

        onShowPhotos() {
            this.store.curWeek.msgShown = false;
            if (this.store.curWeek.photos.length) {
                this.store.curWeek.photoShown = !this.store.curWeek.photoShown;
            }
        },

        photoClicked(photo) {
            if (!photo.size) {
                photo.size = "S";
            }
            let oldSize = photo.size;
            let newSize = oldSize == "S" ? "L": "S";
            this.$set(photo, 'thumb', photo.thumb.replace("size=" + oldSize, "size="+newSize));
            photo.size = newSize;
        }
    }
};
</script>

<template>
	<md-dialog id="editDialog" :md-active.sync="store.shownEditDialog" v-if="store.curWeek">
        <md-dialog-content>
            <div class="pl md-title">{{store.curWeek.desc}}</div>
            <div class="action-btn" @click="store.shownEditDialog = false">
                <md-button class="md-icon-button md-dense">
                    <md-icon>clear</md-icon>
                </md-button>
            </div>
            <div class="action-btn hide-mobile" @click="goNext()">
                <md-button class="md-icon-button md-dense">
                    <md-icon>arrow_forward</md-icon>
                </md-button>
            </div>
            <div class="action-btn hide-mobile" @click="goPrevious()">
                <md-button class="md-icon-button md-dense">
                    <md-icon>arrow_back</md-icon>
                </md-button>
            </div>
            <div class="colored-flags hide-mobile">
                <ul style="margin:0">
                    <li class="colored-flag" v-for="cf in store.curWeek.coloredFlags" :style="'background-color:' + cf.color">{{cf.name}}</li>
                </ul>
            </div>
            <div class="tag-icons" v-if="c_tagIcons.length > 0">
                <div class="wi-icon-placeholder" v-for="tagicon in c_tagIcons" :class="tagicon"></div>
            </div>
            <md-field>
                <label></label>
                <md-textarea id="week_info_textarea" ref="week_info_textarea" v-model="store.curWeek.editInfo" @keydown="onKeydown($event)" md-autogrow></md-textarea>
            </md-field>
            <!--<div class="pl md-title">{{store.curWeek.desc}}</div>-->
            <div id="msgDialog" v-if="store.curWeek.msgShown">
                <div class="msg-container">
                    <div v-if="!store.curWeek.messages || !store.curWeek.messages.length" class="no-messages">
                        <p>  </p>
                    </div>
                    <div class="md-layout md-gutter">
                        <div class="md-layout-item names-column">
                            <md-table>
                                <md-table-row v-for="(chat, index) in store.curWeek.messages" :key="chat.chat" @click="showChat(chat)">
                                    <md-table-cell style="cursor:pointer">{{chat.chat}} ({{chat.messages.length}})</md-table-cell>
                                </md-table-row>
                            </md-table>                 
                        </div>
                        <div class="md-layout-item" v-show="store.curWeek.curChat.messages.length > 0" >
                            <ul class="chat-column" id="chat_column">
                                <li class="msg" v-for="msg in store.curWeek.curChat.messages" :class="{me: !msg.isin}">
                                    <div class="msg-inner">
                                        <div class="msg-header">
                                            <img class="channel-icon" :src="'/img/icon/' + msg.chan + '.png'" />
                                            <span class="msg-sender">{{msg.sndr}}</span>
                                            <span class="msg-date">{{msg.date | fmtDate}}</span>
                                        </div>
                                    <div class="msg-text" v-html="msg.text"></div>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div id="photoDialog" v-if="store.curWeek.photoShown">
                <template v-for="photo in c_photos">
                <div class="ib photo">
                    <div>({{photo.date | date}})</div>
                    <img style="display:inline-grid" v-bind:src="photo.thumb" @click="photoClicked(photo)" />
                </div>
                </template>
            </div>
        </md-dialog-content>
        <md-dialog-actions>
            <md-button @click="onShowPhotos()" :disabled="!store.curWeek.photoCount"> <span v-show="store.curWeek.photoLoading">...</span><span v-show="store.curWeek.photoCount">({{store.curWeek.photoCount}})</span></md-button>
            <md-button @click="onShowMessages()" :disabled="!store.curWeek.msgCount">C <span v-show="store.curWeek.msgLoading">...</span><span v-show="store.curWeek.msgCount">({{store.curWeek.msgCount}})</span></md-button>
            <md-button @click="onSave(false)"></md-button>
            <md-button @click="onSave(true)">  </md-button>
        </md-dialog-actions>
    </md-dialog>
</template>

<style lang="less">
@import "../assets/wol-vars.less";

#photoDialog {
    min-height: 300px;
    max-height: 500px;
    overflow-y: auto;
}

.photo {
    padding: 2px;
    margin: 8px;
    font-size: 9px;
}

.msg-container {
    clear:both;
    padding: 10px 12px 12px 12px;
}

.no-messages {
    height: 150px;
    text-align:center;
    vertical-align:middle;
}

.names-column {
    max-width: 280px;
    min-height: 500px;
    overflow-y: auto;
}

ul.chat-column {
    width: 400px;
    list-style: none;
    margin: 0 0 0 auto;
    padding: 0 6px;
    background-color: lightblue;
    border-radius: 4px;
    border: 1px solid #555;
    max-height: 500px;
    overflow-y: scroll;
}

li.msg {
    width: 100%;
}

.msg-inner {
    background-color: white;
    box-sizing: border-box;
    max-width: 280px;
    border-radius: 4px;
    padding: 4px;
    margin: 2px 0;
    display: inline-block;
    text-align: left;

    &:first-child {
        margin-top: 4px;
    }

    :last-child {
        margin-bottom: 4px;
    }
}

li.msg.me {
    text-align: right;

    .msg-inner {
        background-color: lightyellow;
    }
}

.chat-title {
    font-size: 16px;
    font-weight: bold;
    padding-top: 5px;
    color: black;
}

.channel-icon {
    width: 12px;
    height: 12px;
}

.msg-header {
    height: 10px;
    line-height: 10px;
    vertical-align: middle;
}

.msg-sender {
    color: #555;
    font-size: 11px;
    font-weight: bold;
}

.msg-date {
    color: gray;
    font-size: 10px;
    float: right;
    margin-left: 4px;
}

.msg-text {
    font-size: 11px;
    line-height: 14px;
    margin-top: 6px;
    overflow: hidden;
}


#editDialog .md-dialog-content:first-child {
    padding-top: 22px;
    width: 800px !important;
}

.curweek-desc {
    font-family: Roboto;
}

@media (max-width:1080px) {
    #editDialog .md-dialog-content:first-child {
        padding-top: 22px;
        width: 600px !important;
    }

    .hide-mobile {
        display: none;
    }
}

#week_info_textarea {
    font-size: 14px;
    line-height: 26px;
    min-height: 78px;
    padding-bottom: 6px;
}

.md-field .md-textarea {
    max-height: 240px;
}

.colored-flag {
    display: inline-flex;
    margin-right: 4px;
    justify-content: center;
    border-radius: 4px;
    color: black;
    font-size: 12px;
    align-items: center;
    box-sizing: border-box;
    padding: 0 4px;
}

.colored-flags {
    float: right;
    position: relative;
    top: -2px;
    margin-right: 5px;
}

.tag-icons {
    float: right;
    display: inline-flex;
    height: 20px;
    align-items: center;
    background-color: #f0f0f0;
    margin-right: 4px;
    padding: 0 4px 0 4px;
}

.action-btn {
    float: right;
    position: relative;
    top: -8px;
}

.action-btn button {
    margin: 2px;
}

.action-btn .md-button .md-ripple {
    padding-left: 8px;
    padding-right: 8px;
    padding-top: 4px;
    display: inline-block;
    margin: 0;
}

.wi-icon-placeholder {
    width: 12px;
    height: 12px;
    margin-right: 4px;
    display: inline-block;
    vertical-align: middle;
    border: 1px solid @window-background;

    &:last-child {
        margin-right: 0;
    }
}



</style>




