export const allowedPNG = ['ng', 'dr', 'buy', 'mov', 'games', 'zz', 'soc', 'major', 'interview', 'quit', 'qbb', 'buh', 'acid', 'crush', 'meet', 'breakup', 'ill', 'sex', 'love', 'exam', 'gig', 'bad', 'death', 'sea', 'abroad'];

export default class WeekModel {
    constructor(startMoment, endMoment, weekNum, info, spans) {
        const nowTime = (new Date()).getTime();
        this.startTime = startMoment._d.getTime();
        this.endTime = endMoment._d.getTime();
        this.year = endMoment.isoWeekYear();   //
        this.yearNum = endMoment.isoWeek();       //   
        this.weekNum = weekNum;                //   
        this.flags = ""; //
        this.spanIds = [];
        this.coloredFlags = [];                //

        this._info = info || "";
        Object.defineProperty(this, 'info', {
            get() { return this._info || ""; },
            set(value) { this._info = value; this.tags = null; }
        }); //

        this.desc = `${this.yearNum} / ${this.weekNum} : ${startMoment.format('L')}  ${endMoment.format('L')}`; // - 
        this.messages = [];                //
        
        this.selected = false;
        this.tagged = false;
        this.future = "";                //  
        this.tags = null;               //  

        this.msgLoading = false;         // 
        this.msgCount = 0;
        this.msgShown = false;
        this.curChat = {
             messages: [], chat: ""
        };

        this.photoCount = 0;
        this.photoLoading = false;
        this.photoShown = false;
        this.photos = [];        

        if (this.startTime > nowTime) {
            this.future = "future";
            this.bgStyle = "";
            this.bgcolor = "white";
        } else {
            spans.forEach(span => {
                if (span.startTime >= this.startTime && span.startTime <= this.endTime ||   //  
                    span.endTime >= this.startTime && span.endTime <= this.endTime ||       //  
                    span.startTime < this.startTime && span.endTime >= this.endTime) {      //    
                    this.flags += `[${span.name}]`;
                    this.spanIds.push(span.id);
                    this.coloredFlags.push({
                        name: span.name,
                        color: span.color
                    });
                }
            });

            if (this.coloredFlags.length === 0) {
                this.bgcolor = "#cccccc";
            } else if (this.coloredFlags.length == 1) {
                this.bgcolor = this.coloredFlags[0].color;
            } else {
                this.bgcolor = this.coloredFlags[0].color;
                this.bgcolor2 = this.coloredFlags[1].color;
            }
        }

        this.leftpx = (this.yearNum - 1) * 20 + 60; // 
    }

    getTags() {
        if (this.tags === null) {
            this._info = this._info || "";
            this.tags = this._info.indexOf("#") < 0 ? [] : [...this._info.matchAll(/#([a-z]+)/g)].map(ti => ti[1]);
        }        
        return this.tags;
    }

    getTagIcons() {
        return this.getTags().map(x => x.replace("#", "")).filter(x => allowedPNG.includes(x)) || [];
    }

    getTagClasses() {
        return this.getTags().map(x => x.replace("#", "")).join(" ");
    }

    hasTag(tag) {
        return this._info.includes(tag);
    }
}


