const params = document.body.getAttribute("serverparams").split(";");

export default  {
    name: params[0] || "alex",
    modules: params[1].split(",") || "wol",
    useLocalServer: Boolean(params[2]) || true,    
    baseUrl: params[3] || "",
    thousand: " ",
    decimal: ","  
};


